'use client';
import { use } from 'react';

export default function Client({products,}: {products: Promise<{id: number; name: string, price: number}[]>
}) {
    const allProducts = use(products)

    return (
        <ul>
            {allProducts.map((p) => (
                <li key={p.id}>{p.name}</li>
            ))}
        </ul>
    );
}
